$dark_green: #3F5D45;
$color_yellow: #FFE180;
$bright_blue: #EAF0ED;

@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);

@mixin size($w,$h) {
	width: $w;
	height: $h; }

* {
	font-family: 'cwTeXYen', sans-serif;
	position: relative;
	box-sizing: border-box;
	padding: 0px;
	margin: 0px; }

html, body {
	width: 100%;
	height: 100%; }

//NAVBAR-------------------------------


.pc_menu {
	height: 100px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.navbar_left img {
		height: 40px;
		width: 220px;
		margin-top: 30px;
		margin-left: 42px; }

	.navbar_right {
		margin-top: 40px;
		margin-right: 42px;
		display: flex; }


	.navbar_right a {
		margin-left: 60px;
		text-decoration: none;
		font-size: 16px;
		color: $dark_green;
		font-weight: bold; }

	.ls {
		margin-top: 5px; } }


#shopping_cart {
	i.material-icons {
		font-size: 24px;
		line-height: 24px;
		color: $dark_green; } }

.mobile_menu {
	display: none;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 18px 30px;

	.icon img {
		@include size(150px, 27px); }

	.hamburger {
		@include size(18px, 12px);

		span {
			@include size(18px, 2px);
			display: inline-block;
			position: relative;
			margin: 15px 0px;
			background-color: $dark_green; }

		span:before, span:after {
			content: "";
			background-color: $dark_green;
			@include size(18px, 2px);
			position: absolute;
			left: 0;
			top: -5px; }
		span:after {
			top: 5px; } }

	#shopping_cart {
		i.material-icons {
			margin-top: 3px; } } }



@media screen and (max-width: 768px) {
	.pc_menu {
		display: none; }
	.mobile_menu {
		display: flex; } }




//HEADER-------------------------------
.header {
	width: 960px;
	height: 496px;
	margin-left: auto;
	margin-right: auto;

	@media screen and (max-width: 992px) {
		max-width: 100%; }


	.image_wrap {
		background-image: url("../images/header_image.jpg");
		width: 100%;
		height: 100%;
		background-position: center center;
		background-size: cover;

		@media screen and (max-width: 768px) {
    		height: 70%; } }

	.selection {
		position: absolute;
		display: flex;
		flex-wrap: wrap;
		height: 256px;
		width: 100%;
		justify-content: center;
		top: 405px;

		@media screen and (max-width: 768px) {
			top: initial; }


		.items_wrap {
			height: 100%;
			width: 260px;
			top: 0px;
			z-index: 4;
			overflow: hidden;
			user-select: none;
			cursor: pointer;

			@media screen and (max-width: 992px) {
				width: 33%; }


			#blur_pic1 {
				background-image: url("../images/recommend_item.jpg"); }
			#blur_pic2 {
				background-image: url("../images/popular_item.jpg"); }
			#blur_pic3 {
				background-image: url("../images/new_item.jpg"); }

			.blur_bg {
				position: absolute;
				width: 100%;
				height: 100%;
				background-size: cover;
				background-position: center center;
				filter: blur(5px);
				transition-duration: 0.2s; }




			.text {
				display: flex;
				justify-content: center;
				background-color: $dark_green;
				height: 100%;
				width: 100%;
				opacity: 0.8; }

			.text:hover {
				background-color: $color_yellow;
				p {
					color: $dark_green;
					font-weight: bold; } }

			p {
				font-size: 24px;
				color: white;
				writing-mode: vertical-rl;
				letter-spacing: 3px;
				text-align: center;


				@media screen and (max-width: 576px) {
					font-size: 20px; } } } } }




//Intro_wrap------------------------------------------------
.intro_wrap {
	margin-top: 347px;

	.why {
		height: 420px;
		background-color: $bright_blue;

		@media screen and (max-width: 576px) {
			height: auto; }

		.content_wrap {
			top: -30px;
			display: flex;
			// flex-wrap: wrap
			width: 905px;
			height: 460px;
			margin-left: auto;
			margin-right: auto;
			justify-content: space-between;

			@media screen and (max-width: 992px) {
				width: 90%; }
			// @media screen and (max-width: 768px)
			// 	width: 100%
			// 	justify-content: center
			@media screen and (max-width: 576px) {
				font-size: 20px;
				justify-content: center;
				flex-wrap: wrap;
				width: 100%; }

			.img {
				@include size(460px, 460px);
				background-position: center center;
				background-size: cover;

				@media screen and (max-width: 992px) {
					width: 44%;
					height: 90%; }
				@media screen and (max-width: 576px) {
					@include size(100%, 250px); } }

			.textbox {
				width: 256px;
				display: flex;
				flex-direction: row-reverse;


				@media screen and (max-width: 576px) {
					writing-mode: initial;
					width: 100%;
					flex-wrap: wrap; }

				p {
					writing-mode: vertical-rl;
					line-height: 30px;
					font-size: 16px;
					color: #8DA291;
					height: 460px;
					padding-top: 70px;
					padding-bottom: 70px;
					padding-left: 64px;

					@media screen and (max-width: 768px) {
						padding-left: 15px; }
					@media screen and (max-width: 576px) {
						writing-mode: initial;
						height: 120px;
						padding: 30px; } } } } }


	#why_make {
		margin-bottom: 105px;

		@media screen and (max-width: 576px) {
			flex-direction: column-reverse; }

		#make_textImg {
			background-image: url("../images/make_textImg.svg");
			width: 89px;
			height: 307px;
			right: 0px;
			margin-left: 20px;
			background-repeat: no-repeat;

			@media screen and (max-width: 576px) {
				background-image: url("../images/sm-橫式-為什麼選擇了做甜點.svg");
				bottom: 600px;
				@include size(196px, 60px);
				margin-left: auto;
				margin-right: auto; } }


		#make_img {
			background-image: url("../images/make_img.jpg"); } }

	#why_eat {
		@media screen and (max-width: 768px) {
			p {
				padding-right: 10px; } }

		@media screen and (max-width: 576px) {
			top: 70px; }

		.textbox {
			@media screen and (max-width: 576px) {
				top: 280px;
				background-color: $bright_blue; } }

		#eat_textImg {
			background-image: url("../images/eat_textImg.svg");
			width: 88px;
			height: 303px;
			background-repeat: no-repeat;

			@media screen and (max-width: 768px) {
				margin-right: 10px; }
			@media screen and (max-width: 576px) {
				top: -320px; }

			@media screen and (max-width: 576px) {
				background-image: url("../images/sm-橫式-為什麼一定要吃甜點.svg");
				bottom: 600px;
				@include size(196px, 60px);
				margin-left: auto;
				margin-right: auto; } }

		#eat_image {
			right: 0px;
			background-image: url("../images/eat_img.jpg");

			@media screen and (max-width: 576px) {
				top: -270px; } } }

	.reasonImg_wrap {
		background-image: url("../images/eat_reason.svg");
		background-repeat: no-repeat;
		height: 327px;
		width: 89px;
		margin-left: auto;
		margin-right: auto;
		margin-top: 95px;
		margin-bottom: 80px;

		@media screen and (max-width: 576px) {
			background-image: url("../images/sm-橫式-想吃甜點是不需要理由的.svg");
			top: 35px;
			width: 226px;
			height: 60px; } } }

//DailySpecial_wrap----------------------------------
.DailySpecial_wrap {
	height: 436px;
	width: 940px;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	user-select: none;

	@media screen and (max-width: 576px) {
		width: 100%;
		justify-content: center; }

	.today_item {
		width: 300px;

		@media screen and (max-width: 576px) {
			// width: 100%
			margin-left: auto;
			margin-right: auto; }

		.top {
			height: 315px;

			@media screen and (max-width: 576px) {
				width: 80%; }

			.img {
				height: 315px;
				width: 300px;
				background-size: cover;
				background-position: center center;

				@media screen and (max-width: 576px) {
					width: 100%; } }

			#todaySpecial_img1 {
				background-image: url("../images/todaySpecial1.jpg"); }

			#todaySpecial_img2 {
				background-image: url("../images/todaySpecial2.jpg"); }

			#todaySpecial_img3 {
				background-image: url("../images/todaySpecial3.jpg"); }

			.tag {
				position: absolute;
				top: 0px;
				writing-mode: vertical-rl;
				left: 10px;
				padding: 10px;
				font-size: 16px;
				letter-spacing: 4px;
				background-color: $dark_green;
				color: $bright_blue; }

			.loved {
				background-image: url("../images/loved.svg");
				width: 22px;
				height: 22px;
				position: absolute;
				top: 19px;
				right: 22px; } }

		.bottom {
			height: 121px;
			display: flex;
			flex-wrap: wrap;
			color: $dark_green;

			.item_name {
				font-size: 20px;
				padding: 14px 33px;
				border-right: solid 1px $bright_blue;
				border-left: solid 1px $bright_blue; }

			.price {
				font-size: 20px;
				font-weight: bold;
				padding: 14px 25px 14px 25px;
				border-right: solid 1px $bright_blue; }

			.addToCart {
				font-size: 24px;
				padding: 16px 90px;
				font-weight: bold;
				background-color: $bright_blue; } } } }


